<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示商城所有的会员信息</li>
      <li>可对会员进行修改、查看、删除等操作</li>
      <li>禁用会员无法进行商城登录</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
        <div class="query-group">
          <label>会员账户</label>
          <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
        </div>
        <div  class="query-group">
          <label>邮箱</label>
          <el-input v-model="queryParams.email" @keyup.native.enter="query"></el-input>
        </div>
        <div  class="query-group">
          <label>手机</label>
          <el-input v-model="queryParams.phone" @keyup.native.enter="query"></el-input>
        </div>
         <div  class="query-group">
          <label>是否启用</label>
          <el-select v-model="queryParams.enabled" @change="query">
            <el-option label="全部" value=" "></el-option>
            <el-option label="是" :value="true"></el-option>
            <el-option label="否" :value="false"></el-option>
          </el-select>
        </div>
        <div  class="query-group">
          <label>会员等级</label>
          <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
        </div>
        <div  class="query-group">
          <label>注册日期</label>
          <cms-picker v-model="queryParams.timer" @change="setTimer"></cms-picker>
        </div>
        <div  class="query-group">
           <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
    <!-- 操作区 -->
    <section class="cms-batch">
      <cms-button icon="add" type="success" size="small" @click="$routerLink('/shopMember/save','save')">新增</cms-button>
      <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
      <cms-button icon="success" type="success" size="small" @click="batchEnabled(ids)">启用</cms-button>
      <cms-button icon="warning" type="warning" size="small" @click="batchDisabled(ids)">禁用</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="username" label="用户名" min-width="180"></el-table-column>
      <el-table-column prop="email" label="邮箱" min-width="180"></el-table-column>
      <el-table-column prop="phone" label="手机" min-width="140"></el-table-column>
      <el-table-column prop="balance" label="余额/积分" min-width="140">
        <template slot-scope="scope">
            <span v-if="scope.row.memberAccount">{{scope.row.memberAccount.balance}}/{{scope.row.memberAccount.score}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="levelName" label="成长值/会员等级" min-width="140">
        <template slot-scope="scope">
       <span v-if="scope.row.memberAccount&&scope.row.memberLevel">
          {{scope.row.memberAccount.exp}}/{{scope.row.memberLevel.levelName}}
       </span>
        </template>
      </el-table-column>
      <el-table-column prop="enabled" label="是否启用" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.enabled"  :id='scope.row.id' @change="setDisable"></cms-switch>
        </div>
      </el-table-column>
      <el-table-column prop="createTime" label="注册日期" min-width="160">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="lastLoginTime" label="最后登录时间" min-width="160"></el-table-column>
      <el-table-column prop="lastLoginIp" label="最后登录IP" min-width="160"></el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/shopMember/edit','edit',{id:scope.row.id})">修改</cms-button>
          <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { memberList, memberDelete, memberDisable, memberEnable } from './../../../../api/users/user.js'
var defaultData = {
  email: '',
  phone: '',
  enabled: '',
  level: '',
  username: '',
  startTime: '',
  endTime: '',
  size: 15,
  page: 1,
  isOpenStore: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      value: true
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(memberDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(memberDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    batchEnabled(ids) {
      // 批量启用
      if (this.batchStatus) {
        this.loading = true
        memberEnable({ ids: ids }).then(res => {
          this.loading = false
          if (res.code === 200) {
            console.log(this.tableData)
            this.getTableData(memberList, this.queryParams)
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage('启用失败')
          }
        })
      } else {
        this.$errorMessage('请至少选择一条数据')
      }
    },
    batchDisabled(ids) {
      // 批量禁用
      if (this.batchStatus) {
        this.loading = true
        memberDisable({ ids: ids }).then(res => {
          this.loading = false
          if (res.code === 200) {
            console.log(this.tableData)
            this.getTableData(memberList, this.queryParams)
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage('禁用失败')
          }
        })
      } else {
        this.$errorMessage('请至少选择一条数据')
      }
    },
    setDisable(status, id) {
      if (status) {
        memberEnable({ ids: [id] }).then(res => {
          // 启用
          if (res.code === 200) {
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage('启用失败')
          }
        })
      } else {
        memberDisable({ ids: [id] }).then(res => {
          // 禁用
          if (res.code === 200) {
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage('禁用失败')
          }
        })
      }
    }
  },
  created() {
    this.getTableData(memberList, this.queryParams)
  }
}
</script>

<style>
</style>



